.style .token.string {
  color: #9a6e3a;
  /* This background color was intended by the author of this theme. */
  background: rgba(255, 255, 255, 0.5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a;
}

.token.function,
.token.class-name {
  color: #DD4A68;
}

.token.regex,
.token.important,
.token.variable {
  color: #e90;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

pre[data-line] {
  position: relative;
  padding: 1em 0 1em 3em;
}

.line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  padding: inherit 0;
  margin-top: 1em;
  /* Same as .prism’s padding-top */
  background: rgba(153, 122, 102, 0.08);
  background: linear-gradient(to right, rgba(153, 122, 102, 0.1) 70%, rgba(153, 122, 102, 0));
  pointer-events: none;
  line-height: inherit;
  white-space: pre;
}

@media print {
  .line-highlight {
    /*
     * This will prevent browsers from replacing the background color with white.
     * It's necessary because the element is layered on top of the displayed code.
     */
    -webkit-print-color-adjust: exact;
    color-adjust: exact;
  }
}
.line-highlight:before,
.line-highlight[data-end]:after {
  content: attr(data-start);
  position: absolute;
  top: 0.4em;
  left: 0.6em;
  min-width: 1em;
  padding: 0 0.5em;
  background-color: rgba(153, 122, 102, 0.4);
  color: #f5f2f0;
  font: bold 65%/1.5 sans-serif;
  text-align: center;
  vertical-align: 0.3em;
  border-radius: 999px;
  text-shadow: none;
  box-shadow: 0 1px white;
}

.line-highlight[data-end]:after {
  content: attr(data-end);
  top: auto;
  bottom: 0.4em;
}

.line-numbers .line-highlight:before,
.line-numbers .line-highlight:after {
  content: none;
}

pre[id].linkable-line-numbers span.line-numbers-rows {
  pointer-events: all;
}

pre[id].linkable-line-numbers span.line-numbers-rows > span:before {
  cursor: pointer;
}

pre[id].linkable-line-numbers span.line-numbers-rows > span:hover:before {
  background-color: rgba(128, 128, 128, 0.2);
}

pre[class*=language-].line-numbers {
  position: relative;
  padding-left: 3.8em;
  counter-reset: linenumber;
}

pre[class*=language-].line-numbers > code {
  position: relative;
  white-space: inherit;
}

.line-numbers .line-numbers-rows {
  position: absolute;
  pointer-events: none;
  top: 0;
  font-size: 100%;
  left: -3.8em;
  width: 3em;
  /* works for line-numbers below 1000 lines */
  letter-spacing: -1px;
  border-right: 1px solid #999;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.line-numbers-rows > span {
  display: block;
  counter-increment: linenumber;
}

.line-numbers-rows > span:before {
  content: counter(linenumber);
  color: #999;
  display: block;
  padding-right: 0.8em;
  text-align: right;
}

/**
 * Overrides for the Prism library.
 */
:root code[class*=language-],
:root pre[class*=language-] {
  font-size: 0.875rem;
  font-family: "IBM Plex Mono", monospace;
  text-shadow: none;
  color: #707079;
  line-height: 1.75;
}
:root code[class*=language-] .line-marker,
:root pre[class*=language-] .line-marker {
  pointer-events: none;
  user-select: none;
  opacity: 0.5;
}
:root :not(pre) > code[class*=language-],
:root pre[class*=language-] {
  background: #f8f8f9;
}
:root pre[class*=language-] {
  margin: 0 0 16px;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.03);
  border-radius: 4px;
}
:root pre[class*=language-]::before {
  content: attr(data-language);
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  font-family: Inter, system-ui, sans-serif;
  font-size: 0.75rem;
  text-transform: uppercase;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.15s;
}
:root pre[class*=language-]:hover::before {
  opacity: 1;
}
:root .line-numbers .line-numbers-rows {
  border-right: none;
  margin-top: -0.3em;
}
:root .line-numbers .line-numbers-rows > span::before {
  color: #707079;
}
:root .token.comment, :root .token.prolog, :root .token.doctype, :root .token.cdata {
  color: #707079;
}
:root .token.punctuation {
  color: #707079;
}
:root .token.property, :root .token.tag, :root .token.boolean, :root .token.number, :root .token.constant, :root .token.symbol, :root .token.deleted {
  color: #248142;
}
:root .token.selector, :root .token.attr-name, :root .token.string, :root .token.char, :root .token.builtin, :root .token.inserted {
  color: #004880;
}
:root .token.function, :root .token.class-name {
  color: #da2530;
}
:root .token.regex, :root .token.important, :root .token.variable {
  color: #886f24;
}
:root .token.atrule, :root .token.attr-value, :root .token.keyword {
  color: #2d73bc;
}